<template>
    <div>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
        <router-link to="/" tag="div" class="header-abs" v-show="showAbs">
            <div class="iconfont header-abs-back">&#xe61e;</div>
        </router-link>
        <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
            <router-link to="/">
                <div class="iconfont header-fixed-back">&#xe61e;</div>
            </router-link>
            景点详情
        </div>
    </div>
</template>

<script>
export default {
    name:"DetaiHeader",
    data() {
        return {
            showAbs:true,
            i:0 ,//鼠标移动的位置
            opacityStyle:{
                opacity:0
            }
        }
    },
    methods:{ 
        handleScroll() { 
            var scrollTop = window.pageYOffset || document.documentElement.scrollTop
            var scroll = scrollTop - this.i
            this.i = scrollTop //监听鼠标滚动
            
            // console.log(this.i)
            let top = document.documentElement.scrollTop
            if(top > 60) {
                let opacity = top / 140
                opacity = opacity > 1 ? 1 : opacity
                this.opacityStyle = { opacity }
                this.showAbs = false
            } else {
                this.showAbs = true
            }
        }
    },
    mounted() { 
        window.addEventListener('scroll', this.handleScroll)
    },
    beforeDestroy() {
         window.removeEventListener('scroll', this.handleScroll)
    },
}
</script>

<style lang="stylus" scoped>
 @import "~styles/varibles.styl"
    .header-abs
        position:absolute;
        left:.2rem;
        top:.2rem;
        width:.8rem;
        height:.8rem;
        line-height:.8rem;
        border-radius:.4rem;
        text-align:center;
        background:rgba(0, 0, 0, .8);

        .header-abs-back
            color:#fff;
            font-size:.4rem;

    .header-fixed
        position:fixed;
        top:0;
        left:0;
        right:0;
        height:$headerHeight;
        line-height:$headerHeight;
        background:$bgColor;
        text-align:center;
        color:$lightColor;
        font-size:.32rem;
    
    .header-fixed-back
        position:absolute
        width:.64rem;
        text-align:center;
        font-size:.4rem;
        color:$lightColor;
        top:0rem;
        left:0;
</style>



