<template>
    <div>
        <router-link tag="div" class="banner" :to="'/CommonGallary/' + detail.id">
            <img class="banner-img"
                :src="detail.img" alt=""
            >
            <div class="banner-info">
                
                <div class="banner-title">
                    {{detail.bannerTitle}}        
                </div>
                 <div class="banner-number">
                    <span class="banner-icon">&#xe60f;</span>
                    {{detail.number}}
                </div>
            </div>
        </router-link>
    </div>
</template>

<script>
export default {
    name: 'DetailBanner',
    props:{
        detail:Object,
        img:String,
        bannerTitle:String,
        number:String,
        pid:String
    }
}
</script>

<style lang="stylus" scoped>
    .banner
        position:relative;
        overflow:hidden;
        height:0;
        padding-bottom:55%;
        
        .banner-img
            width:100%;

        .banner-info
            display:flex;
            position:absolute;
            left:0;
            right:0;
            bottom:0;
            line-height:.6rem;
            color:#fff;
            background-image:linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.6))
        
            .banner-title
                flex:1;
                font-size:.32rem;
                padding:0 .2rem;

            .banner-number
                height:.32rem;
                line-height:.32rem;
                padding:0 .4rem
                margin-top:.14rem;
                border-radius:.2rem;
                background:rgba(0, 0, 0, .3rem)

                .banner-icon
                    font-size:.24rem;

        .banner-left
            position:absolute;
            top:.15rem;
            left:.15rem;
            background:rgba(0, 0, 0, 0.6);
            border-radius:50%;
            height:.8rem;
            line-height:.8rem;
            width:.8rem;
            text-align:center;
            color:#fff;
            font-size:.4rem;
</style>



