<template>
    <div>
        <detail-banner 
            :detail="detail" :pid="pid" :img="img" 
            :bannerTitle="bannerTitle" 
            :number="number"
        >
        </detail-banner>

        <detail-list 
            :categoryList="categoryList" 
            :children="children"
        >
        </detail-list>

        <detail-header></detail-header>
        <div class="content"></div>
    </div>
</template>

<script>
import axios from "axios";
import DetailBanner from "./components/Banner";
import DetailList from "./components/List";
import DetailHeader from "./components/Header";

export default {
  name: "Detail",
  data() {
    return {
      detail: {},
      img: "",
      bannerTitle: "",
      number: "",
      categoryList: [], //详情页主体
      title: "",
      children: [],
      pid: this.$route.params.id
    };
  },
  components: {
    DetailBanner,
    DetailList,
    DetailHeader
  },
  methods: {
    getItemInfo() {
      axios.get("static/mock/detail.json").then(res => {
        let obj = (this.detail = res.data.data.detail.filter(
          el => el.id == this.pid
        )[0]); //详情页第一个数组
        const img = obj.img;
        const bannerTitle = obj.bannerTitle;
        const number = obj.number;

        let list = (this.categoryList = res.data.data.categoryList);
        const title = list.title; //详情页主体
        const children = list.children;
      });
    }
  },
  mounted() {
    this.getItemInfo()
  }
};
</script>

<style lang="stylus" scoped>
    .content
        height:20rem;
</style>



