<template>
    <div>
        <div class="item" v-for="(item, index) of categoryList" :key="index">
            <div class="item-title border-bottom">
                <span class="item-title-icon"></span>
                {{ item.title }}
            </div>
            <div v-if="item.children" class="item-children">  
                <detail-list :categoryList="item.children"></detail-list>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name:"DetailList",
    props:{
        categoryList:Array,
        children:Array
    }
}
</script>

<style lang="stylus" scoped>
    .item-title
        line-height:.8rem;
        font-size:.32rem;
        padding:0 .2rem;

        .item-title-icon
            position: relative;
            left: .06rem;
            top: .06rem;
            width: .36rem;
            height: .36rem;
            line-height:.36rem;
            margin-right: .1rem;
            background-size: .4rem 3rem;
    
    .item-children
        padding:0 .2rem;
</style>



